@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1000;
    background-color: #1f1f1f;
    padding: 20px 40px;
    transition: top 0.3s ease;
  }
  
  .header.hidden {
    top: -100px; /* Oculta hacia arriba */
  }
  
  .header.visible {
    top: 0;
  }
.logo {
    position: relative;
    font-size: 25px;
    color: #ededed;
    text-decoration: none;
    font-weight: 600;
}

.logo::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background: #081b29;
    animation: showRight 1s ease forwards;
    animation-delay: .4s;
}

.navbar {
    display: flex;
}

.navbar a {
    font-size: 18px;
    color: #ededed;
    text-decoration: none;
    font-weight: 500;
    margin-left: 35px;
    transition: color 0.3s ease;
}

.navbar a:hover,
.navbar a.active {
    color: #00abf0;
}

/* -------------------- */
/* Responsive agregado  */
/* -------------------- */

.menu-toggle {
    display: none;
    font-size: 30px;
    color: #ededed;
    cursor: pointer;
    z-index: 101;
}

@media (max-width: 768px) {
    .menu-toggle {
        display: block;
    }

    .navbar {
        position: absolute;
        top: 100%;
        right: 10%;
        background: #081b29;
        flex-direction: column;
        align-items: flex-start;
        width: 200px;
        padding: 20px;
        border-radius: 10px;
        box-shadow: 0 0 10px rgba(0,0,0,0.5);
        display: none;
    }

    .navbar.active {
        display: flex;
    }

    .navbar a {
        margin: 10px 0;
        margin-left: 0;
        width: 100%;
    }
}

@media (max-width: 576px) {
    .home-content h1 {
        font-size: 40px;
    }

    .home-content h3 {
        font-size: 24px;
    }

    .btn-box {
        flex-direction: column;
        height: auto;
        grid-gap: 10px;
        gap: 10px;
    }

    .btn-box a {
        width: 100%;
        height: 45px;
    }
}

