.contacto {
    padding: 80px 20px;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, #081b29, #081b29, #2c5364); 
    color: #fff;
  }
  
  .contacto-content {
    max-width: 600px;
    width: 100%;
    background: rgba(255, 255, 255, 0.05);
    padding: 40px;
    border-radius: 20px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
  }
  
  .contacto-content h1 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 2em;
    color: #00abf0;
  }
  
  .contacto-content p {
    text-align: center;
    margin-bottom: 30px;
    font-size: 1.1em;
  }
  
  form {
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
  }
  
  form label {
    font-weight: 500;
  }
  
  form input,
  form textarea {
    padding: 10px;
    border: none;
    border-radius: 10px;
    background-color: #ffffff10;
    color: #fff;
    font-size: 1em;
    outline: none;
    resize: none;
  }
  
  form input:focus,
  form textarea:focus {
    background-color: #ffffff20;
    border: 1px solid #00abf0;
  }
  
  form button {
    padding: 10px;
    background-color: #00abf0;
    border: none;
    border-radius: 10px;
    color: #fff;
    font-size: 1em;
    cursor: pointer;
    transition: 0.3s;
  }
  
  form button:hover {
    background-color: #0088cc;
  }
  
  .info-contacto {
    margin-top: 30px;
    text-align: center;
  }
  
  .info-contacto p {
    margin-bottom: 15px;
    font-size: 1em;
  }
  
  .btn-whatsapp {
    display: inline-block;
    padding: 10px 20px;
    background-color: #25D366;
    color: #fff;
    border-radius: 10px;
    font-size: 1em;
    text-decoration: none;
    transition: 0.3s;
  }
  
  .btn-whatsapp i {
    margin-right: 8px;
    vertical-align: middle;
    font-size: 1.2em;
  }
  
  .btn-whatsapp:hover {
    background-color: #1ebe57;
  }
  
/* --- SOBRE MÍ --- */
.sobre-mi {
  padding: 120px 20px 80px; /* top - lados - bottom */
  background-color: #0c1e2c;
  color: #fff;
  text-align: center;
}

  .contenido-sobre-mi h1 {
    position: relative;
    font-size: 2.5em;
    margin-bottom: 20px;
    color: #00abf0;
  }
  
  .contenido-sobre-mi p {
    max-width: 800px;
    margin: auto;
    font-size: 1.1em;
    line-height: 1.8;
  }
  
  /* --- PROYECTOS --- */
  .proyectos {
    padding: 80px 20px;
    background: #0c1e2c;
    color: #fff;
    text-align: center;
  }
  
  .proyectos h2 {
    font-size: 2.5em;
    margin-bottom: 40px;
    color: #00abf0;
  }
  
  .proyectos-container {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 30px;
    gap: 30px;
    justify-content: center;
  }
  
  .proyecto-card {
    background: #1e2b38;
    width: 280px;
    height: 200px;
    border-radius: 15px;
    overflow: hidden;
    position: relative;
    transition: transform 0.3s ease;
    cursor: pointer;
    box-shadow: 0 0 20px rgba(0,0,0,0.4);
  }
  
  .proyecto-card:hover {
    transform: scale(1.05);
  }
  
  .proyecto-card img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    transition: opacity 0.5s ease;
  }
  
  .proyecto-card .contenido {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 171, 240, 0.9);
    color: #fff;
    padding: 20px;
    opacity: 0;
    transition: opacity 0.3s ease;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  .proyecto-card:hover .contenido {
    opacity: 1;
  }
  

